class Brackets extends rwxMatrix
{
	constructor()
	{
		super('brackets', true, 70, 1, 1200, THREE);
		this.loaded = false;
		this.destroyDemo();
		this.camera.position.x = -3;
		this.directLight.position.set(10 , -20, 15);
		this.ambience.color = new this.THREE.Color("#1342a0");

		this.mouseTrack = new rwxMouseTrack(this.parent.parentNode);

		this.loadFont().then((res)=>{
			this.mainBrackets(res);
			this.backgroundCharacters(res);
			this.loaded=true;
			this.bracketAnimation();
		});
	}

	bracketAnimation(){
		const animations = [
			{
				from:[this.pos1.x,this.pos1.y,this.pos2.x,this.pos2.y], 
				to:[this.pos1.x+30,this.pos1.y+30,this.pos2.x-30,this.pos2.y-30], 
				easing:'easeInElastic', 
				duration:3000,
				delay:1000
			},
			{
				to:[this.pos1.x, this.pos1.y, this.pos2.x, this.pos2.y],
				easing:'easeInElastic', 
				duration:3000
			},
			{
				from: 0,
				to:1080,
				easing: 'easeOutBack',
				duration: 5000,
				delay:1000
			},
			{
				from:[this.pos1.x,this.pos1.y,this.pos2.x,this.pos2.y],
				control: [
					{cp1:this.pos1.x-100, cp2:this.pos1.x+100},
					{cp1:this.pos1.y-10, cp2:this.pos1.y+10},
					{cp1:this.pos2.x+100, cp2:this.pos2.x-100},
					{cp1:this.pos2.y-10, cp2:this.pos2.y+10},
				],
				to:[this.pos1.x,this.pos1.y,this.pos2.x,this.pos2.y],
				easing: 'easeInOutCubic',
				duration: 2000,
				delay:1000
			},
			{
				from:0,
				to:360,
				easing:'easeInQuint',
				duration:2000,
				delay:1000
			},
			{
				from:0,
				to:360,
				easing:'easeOutQuint',
				duration:2000,
			},
			{
				from: this.pos1.z,
				to: -600,
				easing: 'easeInQuint',
				duration: 1500,
				delay:1000
			},
			{
				from: -600,
				to: this.pos1.z,
				easing: 'easeOutQuint',
				duration:1500
			}
		];

		this.flickerAnimation = new rwxAnimationChain({
			sequence: [
				{
					from: 1,
					to: 0.33,
					control: {cp1:rwxMath.randomInt(0.33, 0.53), cp2:rwxMath.randomInt(0.23, 0.43)},
					easing: 'easeInOutElastic',
					duration: 3000,
				},
				{
					from: 0.33,
					to: 1,
					control: {cp1:rwxMath.randomInt(0.83, 0.63), cp2:rwxMath.randomInt(0.73, 0.93)},
					easing: 'easeInOutElastic',
					duration: 2000,
				}
			],
			loop: true
		})

		this.bracketAnimations = new rwxAnimationChain({
			sequence:animations,
			loop:true
		});
	}

	stop()
	{
		this.unhook();
		this.mouseTrack.remove();
	}

	mainBrackets(res)
	{
		let mainConf = this.getFontConfig(res);
		this.pos1 = window.innerWidth < 700 ? new this.THREE.Vector3(40, 80, -300) : new this.THREE.Vector3(70, 80, -230);
		this.pos2 = window.innerWidth < 700 ? new this.THREE.Vector3(-40, -80, -300) : new this.THREE.Vector3(-70, -80, -230);
		this.bracket1 = this.createBracket('}', this.pos1, mainConf);
		this.bracket2 = this.createBracket('{', this.pos2, mainConf);
	}

	backgroundCharacters(res)
	{
		this.toRotate = [];
		const z = -1000;
		let bg = ['#', '|', '[', ']', '?', '<', '>', '.', ',', '/', '-', '"', '=', '%', '(', ')', '...', '*', ';', '+'];
		rwxMisc.shuffleArray(bg);
		let multiplier = 1;
		if(window.innerWidth < 500){multiplier=0.5}
		if(window.innerWidth > 1200){
			multiplier=1.5;
			bg.push('[', ']', '(', ')');
		}
		const bgpos = [
			new this.THREE.Vector3(-450*multiplier, 600, z),
			new this.THREE.Vector3(-270*multiplier, 533, z),
			new this.THREE.Vector3(33*multiplier, 569, z),
			new this.THREE.Vector3(-146*multiplier, 312, z),
			new this.THREE.Vector3(450*multiplier, 548, z),
			new this.THREE.Vector3(600*multiplier, 292, z),
			new this.THREE.Vector3(700*multiplier, 498, z),
			new this.THREE.Vector3(715*multiplier, 111, z),
			new this.THREE.Vector3(500*multiplier, 23, z),
			new this.THREE.Vector3(750*multiplier, -40, z),
			new this.THREE.Vector3(-723*multiplier, 100, z),
			new this.THREE.Vector3(-597*multiplier, -130, z),
			new this.THREE.Vector3(-783*multiplier, -303, z),
			new this.THREE.Vector3(-699*multiplier, -600, z),
			new this.THREE.Vector3(-636*multiplier, -417, z),
			new this.THREE.Vector3(193*multiplier, -487, z),
			new this.THREE.Vector3(-402*multiplier, -584, z),
			new this.THREE.Vector3(-37*multiplier, -620, z),
			new this.THREE.Vector3(387*multiplier, -583, z),
			new this.THREE.Vector3(-433*multiplier, -30, z),
			new this.THREE.Vector3(-703*multiplier, 557, z),
			new this.THREE.Vector3(-743*multiplier, 289, z),
			new this.THREE.Vector3(700*multiplier, -300, z),
			new this.THREE.Vector3(657*multiplier, -567, z)
		];
		let otherConf = this.getFontConfig(res);
		bg.map((l, i)=>{
			const geo = new this.THREE.TextGeometry( l, otherConf);
			let s = this.addShape(geo, new this.THREE.MeshPhongMaterial({wireframe:true, flatShading:true, opacity:0.1, transparent:true}));
			let position = bgpos[i];
			s.position.z = position.z;
			s.position.x = position.x;
			s.position.y = position.y;
			let obj = {
				mesh:s,
				directions:[],
				positions: {x:position.x, y:position.y, z: position.z},
				drag:  Math.random() /  233,
				theta: Math.floor(Math.random() * (20-2+1) + 2),
				lastMouse: this.mouseTrack.lastmouse,
				speed: (rwxMath.randomInt(1,2) / 100)
			}
			let dirs = ['x', 'y'];
			rwxMisc.shuffleArray(dirs);
			let no = rwxMath.randomInt(1,2);
			for(let i=0;i<no;i++)
			{
				obj.directions.push(dirs[i]);
			}
			this.toRotate.push(obj);
			return false;
		});
	}

	loadFont()
	{
		const loader = new this.THREE.FontLoader();
		return new Promise((resolve, reject)=>{
			resolve(loader.parse(Font));
		})	
	}

	getFontConfig(font)
	{
		return {
			font,
			size: 70,
			height: 5,
			curveSegments: 12,
			bevelEnabled: true,
			bevelThickness: 5,
			bevelSize: 3,
			bevelOffset: 0,
			bevelSegments: 10			
		}
	}

	createBracket(text, pos, settings)
	{
		const geometry = new this.THREE.TextGeometry( text, settings);
		let bracket = this.addShape(geometry, new this.THREE.MeshPhongMaterial({transparent: true}), false);
		bracket.position.set(-25, -25, -5);
    let pivot = new this.THREE.Group();
    pivot.position.set(pos.x, pos.y, pos.z);
    this.scene.add( pivot );
    pivot.add( bracket );	
    pivot.rotation.z = rwxGeometry.toRadians(45);
    return pivot;
	}

	animate()
	{
		if(this.loaded)
		{
			this.bracketAnimations.animate([
				(x, y, x2, y2)=>{
					this.bracket1.position.x = x;
					this.bracket1.position.y = y;
					this.bracket2.position.x = x2;
					this.bracket2.position.y = y2;
				},
				(x, y, x2, y2)=>{
					this.bracket1.position.x = x;
					this.bracket1.position.y = y;
					this.bracket2.position.x = x2;
					this.bracket2.position.y = y2;
				},
				(r)=>{
					this.bracket1.rotation.y = rwxGeometry.toRadians(r);
					this.bracket2.rotation.y = -rwxGeometry.toRadians(r);
				},
				(x, y, x2, y2)=>{
					this.bracket1.position.x = x;
					this.bracket1.position.y = y;
					this.bracket2.position.x = x2;
					this.bracket2.position.y = y2;
				},
				(r)=>{
					this.bracket1.rotation.x = -rwxGeometry.toRadians(r);
					this.bracket2.rotation.x = rwxGeometry.toRadians(r);
				},
				(r)=>{
					this.bracket1.rotation.x = rwxGeometry.toRadians(r);
					this.bracket2.rotation.x = -rwxGeometry.toRadians(r);
				},
				(z)=>{
					this.bracket1.position.z = z;
					this.bracket2.position.z = z;
				},
				(z)=>{
					this.bracket1.position.z = z;
					this.bracket2.position.z = z;
				},
			]);

			this.flickerAnimation.animate([
				(o)=>{
			    this.bracket1.children[0].material.opacity = o;
			    this.bracket2.children[0].material.opacity = o;				
				},
				(o)=>{
			    this.bracket1.children[0].material.opacity = o;
			    this.bracket2.children[0].material.opacity = o;				
				}
			]);

			this.toRotate.map((tr)=>{
				tr.directions.map((trd)=>{
					tr.mesh.rotation[trd] += tr.speed;
					return false;
				})
  			let x = tr.positions.x + (tr.lastMouse.x/tr.theta);
		    let y = tr.positions.y + (tr.lastMouse.y/tr.theta);	
		    tr.mesh.position.x = x;
		    tr.mesh.position.y = y;
		    // tr.mesh.position.z = tr.positions.z + (Math.abs(x)/10)
		    tr.lastMouse.x += (this.mouseTrack.parallaxmouse.x - tr.lastMouse.x) * tr.drag;
		    tr.lastMouse.y += (this.mouseTrack.parallaxmouse.y - tr.lastMouse.y) * tr.drag;
		    return false;
			});
		}
	}
}

export default Brackets;